function randomNumber(end, start) { return Math.floor(Math.random() * end) + (start || 0); }

export default function ListItem(props) {
    return (
        <div onClick={props.onClick} style={{ 
            width: '200px',
            height: '200px',
            backgroundImage: `url(/static/images/${props.item.name}.jpg)`,
            backgroundSize: '100%',
            backgroundRepeat: 'no-repeat',
            marginRight: '2em'
        }}>
            <div style={{
                background: `rgba(${[randomNumber(255), randomNumber(255), randomNumber(255)].join(',')},0.5)`,
                height: '100%',
                width: '100%'
            }}>
                <div style={{
                    textAlign: 'center',
                    lineHeight: '200px',
                    color: 'white',
                    height: '100%',
                    fontSize: '2em',
                    width: '100%',
                    textShadow: '0px 0px 5px rgba(0,0,0,0.75)',
                    verticalAlign: 'middle'
                }}>{props.item.label}</div>
            </div>
        </div>
    );
}